import { useEffect, useRef } from "react";
import { gsap } from "gsap";

type Props = {
  togglePlay: () => void;
  isPlay: boolean;
};

const ButtonPlayer = (props: Props) => {
  const ref = useRef<HTMLButtonElement>(null);

  useEffect(() => {
    const body = document.body;
    const trigger = body.querySelector("header");
    const element = ref.current;

    const tlIn = gsap.timeline({
      scrollTrigger: {
        trigger: trigger,
        scrub: true,
        start: "bottom bottom-=200px",
        end: "+=200px",
      },
    });

    if (element) {
      tlIn.from(element, { opacity: 0, duration: 0.5 });
    }
  }, []);

  return (
    <button
      className="w-12 h-12 bg-golden border-golden fixed bottom-[50px] right-[20px] rounded-full flex justify-center items-center shadow-md"
      onClick={props.togglePlay}
      ref={ref}
    >
      {props.isPlay && (
        <span className="material-symbols-outlined text-white">volume_up</span>
      )}
      {!props.isPlay && (
        <span className="material-symbols-outlined text-white">volume_off</span>
      )}
    </button>
  );
};

export default ButtonPlayer;
