import { gsap } from "gsap";
import { useEffect, useRef } from "react";

const Quote = () => {
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const element = ref.current;
    const quotePrimary = element?.querySelector("#quote-primary");
    const quoteSource = element?.querySelector("#quote-source");
    const quoteSecondary = element?.querySelector("#quote-secondary");

    const tlIn = gsap.timeline({
      scrollTrigger: {
        trigger: element,
        scrub: true,
        start: "top bottom-=200px",
        end: "+=300px",
      },
    });

    if (quotePrimary) {
      tlIn.fromTo(
        quotePrimary,
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.5 }
      );
    }

    if (quoteSource) {
      tlIn.fromTo(
        quoteSource,
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.5 }
      );
    }

    if (quoteSecondary) {
      tlIn.fromTo(
        quoteSecondary,
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.5 }
      );
    }
  }, []);

  return (
    <section className="py-16 relative pattern-bg" ref={ref}>
      <div className="max-w-6xl mx-auto">
        <h3 className="text-lg text-center font-alice italic text-gray-700 px-4 md:px-0">
          <span className="inline-block" id="quote-primary">
            Ihaiva stam ma vi yaustam, visvam ayur vyasnutam, kridantau putrair
            naptrbhih, modamanau sve grhe.
          </span>
          <br />
          <span className="inline-block" id="quote-source">
            (Rg Veda X.85.42)
          </span>
          <br />
          <br />
          <span className="inline-block" id="quote-secondary">
            Wahai pasangan suami-istri, semoga kalian tetap bersatu dan tidak
            pernah terpisahkan. Semoga kalian mencapai hidup penuh kebahagiaan,
            tinggal di rumah yang penuh kegembiraan bersama seluruh keturunanmu.
          </span>
        </h3>
      </div>
    </section>
  );
};

export default Quote;
