import { useEffect, useRef } from "react";
import { gsap } from "gsap";

const Video = () => {
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const element = ref.current;
    const title = element?.querySelector("#video-title");

    const tlIn = gsap.timeline({
      scrollTrigger: {
        trigger: element,
        scrub: true,
        start: "top bottom-=200px",
        end: "+=100px",
      },
    });

    if (title) {
      tlIn.fromTo(
        title,
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.5 }
      );
    }
  }, []);

  return (
    <section className="py-16" ref={ref}>
      <div className="max-w-6xl mx-auto">
        <h2
          className="text-6xl md:text-7xl text-center font-qwitcher-grypen text-golden mb-14"
          id="video-title"
        >
          Cerita Kami
        </h2>
        <iframe
          className="w-full h-[300px] md:h-[600px]"
          src="https://www.youtube.com/embed/4JBfNfbb_y8"
          title="YouTube video player"
          frameBorder="0"
          allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
          allowFullScreen
        ></iframe>
      </div>
    </section>
  );
};

export default Video;
