import { useEffect, useRef } from "react";
import { gsap } from "gsap";

const Footer = () => {
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const element = ref.current;
    const title = element?.querySelector("#footer-title");

    const tlIn = gsap.timeline({
      scrollTrigger: {
        trigger: element,
        scrub: true,
        start: "top bottom-=100",
        end: "bottom bottom",
      },
    });

    if (title) {
      tlIn.fromTo(
        title,
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.5 }
      );
    }
  }, []);

  return (
    <section className="pt-16 pb-20" ref={ref}>
      <h2
        className="text-7xl text-center font-qwitcher-grypen text-golden"
        id="footer-title"
      >
        Matur Suksma
      </h2>
    </section>
  );
};

export default Footer;
