import dayjs from "dayjs";
import { useEffect, useRef, useState } from "react";

type Countdown = {
  day: number;
  hour: number;
  minute: number;
  second: number;
};

export const useCountdown = () => {
  const [countDown, setCountDown] = useState<Countdown>();

  const calculateDiffDateInterval = useRef<NodeJS.Timeout>();

  useEffect(() => {
    calculateDiffDateInterval.current = setInterval(() => {
      const deadline = dayjs("2022-10-01 11:00:00").toDate().getTime();
      const today = dayjs().toDate().getTime();
      const diff = deadline - today;

      const day = Math.floor(diff / (1000 * 60 * 60 * 24));
      const hour = Math.floor(
        (diff % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)
      );
      const minute = Math.floor((diff % (1000 * 60 * 60)) / (1000 * 60));
      const second = Math.floor((diff % (1000 * 60)) / 1000);

      setCountDown({
        day,
        hour,
        minute,
        second,
      });

      if (diff === 0) {
        clearInterval(calculateDiffDateInterval.current);
      }
    }, 1000);

    return () => {
      if (calculateDiffDateInterval.current) {
        clearInterval(calculateDiffDateInterval.current);
      }
    };
  }, []);

  return countDown;
};
