import { useEffect, useRef } from "react";
import { gsap } from "gsap";
import story1 from "../../assets/images/stories/01.jpg";
import story2 from "../../assets/images/stories/02.jpg";
import story3 from "../../assets/images/stories/03.jpg";
import story4 from "../../assets/images/stories/04.jpg";
import story5 from "../../assets/images/stories/05.jpg";
import story6 from "../../assets/images/stories/06.jpg";
import story7 from "../../assets/images/stories/07.jpg";
import story8 from "../../assets/images/stories/08.jpg";
import story9 from "../../assets/images/stories/09.jpg";
import story10 from "../../assets/images/stories/10.jpg";
import story11 from "../../assets/images/stories/11.jpg";
import story12 from "../../assets/images/stories/12.jpg";
import story13 from "../../assets/images/stories/13.jpg";
import story14 from "../../assets/images/stories/14.jpg";
import story15 from "../../assets/images/stories/15.jpg";
import story16 from "../../assets/images/stories/16.jpg";
import story17 from "../../assets/images/stories/17.jpg";
import story18 from "../../assets/images/stories/18.jpg";

const Story = () => {
  const ref = useRef<HTMLDivElement>(null);

  useEffect(() => {
    const element = ref.current;
    const title = element?.querySelector("#story-title");

    const tlIn = gsap.timeline({
      scrollTrigger: {
        trigger: element,
        scrub: true,
        start: "top bottom-=200px",
        end: "+=100px",
      },
    });

    if (title) {
      tlIn.fromTo(
        title,
        { opacity: 0, y: 20 },
        { opacity: 1, y: 0, duration: 0.5 }
      );
    }
  }, []);

  return (
    <section className="py-16 pattern-bg" ref={ref}>
      <div className="max-w-6xl mx-auto text-center">
        <h2
          className="text-6xl md:text-7xl text-center font-qwitcher-grypen text-golden mb-14"
          id="story-title"
        >
          Momen Bahagia
        </h2>
        <div className="grid gap-5 grid-cols-1 md:grid-cols-4 px-5 md:px-0">
          <div className="w-full">
            <img src={story1} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story2} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story3} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story4} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story5} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story6} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story7} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story8} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story9} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story10} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story11} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story12} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story13} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story14} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story15} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story16} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story17} className="object-cover" alt="Story 1" />
          </div>
          <div className="w-full">
            <img src={story18} className="object-cover" alt="Story 1" />
          </div>
        </div>
      </div>
    </section>
  );
};

export default Story;
